import React, { useContext } from "react";
import { CostomContext } from "../../_context.jsx";
import "./_formAdd.scss";
import "../_inputs.scss";
import "../_buttons.scss";
import AdditionalFields from "./additionalFields.jsx";
import Bathhouse from "../Moduls/bathhouse/bathhouse.jsx";

export default function Middle() {
  const {
    setGazebosTime,
    setChernyean,
    setGazebos,
    setChernyeanTime,
    chernyean,
    gazebos,
    chernyeanTime,
    gazebosTime,
    house,
    // setHouse,
    OutCheckbox,
    setCrib,
    crib,
    // setFIO,
    FIO,
    setOrderServices,
    orderServices,
    // setDateResidence,
    dateResidence,
    LISTTIME,
  } = useContext(CostomContext);

  return (
    <div className="bx24_form">
      <form className="form-user" action="true" method="post">
        <div className="div--form">
          Дом&nbsp;/&nbsp;Апартаменты:
          <input
            className="input"
            type="text"
            name="home_user"
            id="home"
            value={house}
            disabled
            // onChange={(e) => setHouse(e.target.value)}
          />
        </div>
        <div className="div--form">
          Даты проживания:
          <div className="date__residence">
            <div className="date__residence--from">
              <span>С:</span>
              <input
                disabled
                value={dateResidence.From}
                className="input"
                type="date"
                name="dateFrom_user"
                id="dateFrom_user"
                // min={dates}
                // onChange={(e) =>
                //   setDateResidence({ ...dateResidence, From: e.target.value })
                // }
              />
            </div>
            <div className="date__residence--to">
              <span>По:</span>
              <input
                disabled
                value={dateResidence.To}
                className="input"
                type="date"
                name="dateTo_user"
                id="dateTo_user"
                // min={dateResidence.From ? dateResidence.From : dates}
                // onChange={(e) =>
                //   setDateResidence({ ...dateResidence, To: e.target.value })
                // }
              />
            </div>
          </div>
        </div>
        <div className="div--form">
          Фамилия Имя Отчество
          <input
            value={FIO}
            className="input"
            type="text"
            name="FIO_user"
            id="FIO_user"
            disabled
            // onChange={(e) => setFIO(e.target.value)}
          />
        </div>
        <Bathhouse />
        <div
          className={
            chernyean
              ? "bathhouse opencheckbox--show"
              : "bathhouse close--bathhouse"
          }
        >
          <div className="checkbox">
            <input
              type="checkbox"
              name="bathhouse_chernyean"
              id="bathhouse_chernyean"
              checked={chernyean}
              onChange={(e) => {
                OutCheckbox(e.target.checked, setChernyean);
              }}
            />
            <label htmlFor="bathhouse_chernyean">
              Забронировать банный чан
            </label>
          </div>
          {chernyean && (
            <div className="checkbox--time">
              <div>Время:</div>
              <div className="bathhouse--time">
                <input
                  list="inputs_time2"
                  className="input input_time"
                  type="time"
                  min="09:00"
                  max="22:00"
                  name="bathhouse_chernyean_time"
                  value={chernyeanTime}
                  pattern="^([0-1]\d|2[0-3])(:00)?$"
                  onChange={(e) => setChernyeanTime(e.target.value)}
                  onBlur={(e) => {
                    if (e.target.value.length > 4) {
                      setChernyeanTime(
                        e.target.value[0] +
                          e.target.value[1] +
                          e.target.value[2] +
                          "00"
                      );
                    }
                    if (e.target.value.length <= 2) {
                      if (e.target.value.length === 1) {
                        setChernyeanTime("0" + e.target.value + ":00");
                      } else {
                        setChernyeanTime(e.target.value + ":00");
                      }
                    }
                  }}
                />
                <datalist id="inputs_time2">{LISTTIME}</datalist>
              </div>
            </div>
          )}
        </div>
        <div
          className={
            gazebos
              ? "bathhouse opencheckbox--show"
              : "bathhouse close--bathhouse"
          }
        >
          <div className="checkbox">
            <input
              type="checkbox"
              name="bathhouse_gazebos"
              id="bathhouse_gazebos"
              checked={gazebos}
              onChange={(e) => {
                OutCheckbox(e.target.checked, setGazebos);
              }}
            />
            <label htmlFor="bathhouse_gazebos">Забронировать беседку</label>
          </div>
          {gazebos && (
            <div className="checkbox--time">
              <div>Время:</div>
              <div className="bathhouse--time">
                <input
                  list="inputs_time3"
                  className="input input_time"
                  type="time"
                  min="09:00"
                  max="22:00"
                  pattern="^([0-1]\d|2[0-3])(:00)?$"
                  name="bathhouse_gazebos_time"
                  value={gazebosTime}
                  onChange={(e) => setGazebosTime(e.target.value)}
                  onBlur={(e) => {
                    if (e.target.value.length > 4) {
                      setGazebosTime(
                        e.target.value[0] +
                          e.target.value[1] +
                          e.target.value[2] +
                          "00"
                      );
                    }
                    if (e.target.value.length <= 2) {
                      if (e.target.value.length === 1) {
                        setGazebosTime("0" + e.target.value + ":00");
                      } else {
                        setGazebosTime(e.target.value + ":00");
                      }
                    }
                  }}
                />
                <datalist id="inputs_time3">{LISTTIME}</datalist>
              </div>
            </div>
          )}
        </div>
        <div
          className={
            crib.active
              ? "bathhouse opencheckbox--show barbecue"
              : "bathhouse barbecue close--bathhouse"
          }
        >
          <div className="checkbox">
            <input
              type="checkbox"
              name="crib"
              id="crib"
              checked={crib.active}
              onChange={(e) => {
                setCrib({
                  ...crib,
                  active: e.target.checked,
                });
              }}
            />
            <label htmlFor="crib">Установить детскую кроватку</label>
          </div>
          {crib.active && (
            <div className="checkbox--time extraSpace">
              <div>Количество детей:</div>
              <div className="bathhouse--time">
                <input
                  className="input input_time extraSpace_range"
                  type="number"
                  min="1"
                  max="7"
                  id="crib_count"
                  name="crib_count"
                  value={crib.count}
                  onChange={(e) => {
                    setCrib({
                      ...crib,
                      count: e.target.value,
                    });
                  }}
                />
                <div
                  class="item__add"
                  onClick={() => {
                    if (crib.count < 5)
                      setCrib({
                        ...crib,
                        count: crib.count + 1,
                      });
                  }}
                >
                  ‹
                </div>
                <div
                  class="item__remove"
                  onClick={() => {
                    if (crib.count > 1)
                      setCrib({
                        ...crib,
                        count: crib.count - 1,
                      });
                  }}
                >
                  ‹
                </div>
              </div>
            </div>
          )}
        </div>
        <div className="div--form">
          <div>Прочие услуги:</div>
          <div className="form__otherServices">
            <div
              className={
                orderServices.pet
                  ? "checkbox opencheckbox--show bathhouse"
                  : "checkbox bathhouse close--bathhouse"
              }
            >
              <input
                type="checkbox"
                name="pet"
                id="pet"
                checked={orderServices.pet}
                onChange={(e) => {
                  setOrderServices({ ...orderServices, pet: e.target.checked });
                }}
              />
              <label htmlFor="pet">Имеется домашний питомец</label>
            </div>
            <div
              className={
                orderServices.massage
                  ? "checkbox opencheckbox--show bathhouse"
                  : "checkbox bathhouse close--bathhouse"
              }
            >
              <input
                type="checkbox"
                name="massage"
                id="massage"
                checked={orderServices.massage}
                onChange={(e) => {
                  setOrderServices({
                    ...orderServices,
                    massage: e.target.checked,
                  });
                }}
              />
              <label htmlFor="massage">Требуется массаж</label>
            </div>
            <div
              className={
                orderServices.manicure
                  ? "checkbox opencheckbox--show bathhouse"
                  : "checkbox bathhouse close--bathhouse"
              }
            >
              <input
                type="checkbox"
                name="manicure"
                id="manicure"
                checked={orderServices.manicure}
                onChange={(e) => {
                  setOrderServices({
                    ...orderServices,
                    manicure: e.target.checked,
                  });
                }}
              />
              <label htmlFor="manicure">Требуется маникюр</label>
            </div>
            <div
              className={
                orderServices.businessExcursion
                  ? "checkbox opencheckbox--show bathhouse"
                  : "checkbox bathhouse close--bathhouse"
              }
            >
              <input
                type="checkbox"
                name="businessExcursion"
                id="businessExcursion"
                checked={orderServices.businessExcursion}
                onChange={(e) => {
                  setOrderServices({
                    ...orderServices,
                    businessExcursion: e.target.checked,
                  });
                }}
              />
              <label htmlFor="businessExcursion">
                Посетить бизнес экскурсию
              </label>
            </div>
          </div>
        </div>
        <AdditionalFields />
        <div className="form-btn">
          <button className="btn btn-primary" type="submit">
            <span>Подтвердить</span>
          </button>
        </div>
      </form>
    </div>
  );
}
