import React, { createContext, useState } from "react";

export const CostomContext = createContext();

export const Context = (props) => {
  const LISTTIME = (
    <>
      <option value={"09:00"} />
      <option value={"10:00"} />
      <option value={"11:00"} />
      <option value={"12:00"} />
      <option value={"13:00"} />
      <option value={"14:00"} />
      <option value={"15:00"} />
      <option value={"16:00"} />
      <option value={"17:00"} />
      <option value={"18:00"} />
      <option value={"19:00"} />
      <option value={"20:00"} />
      <option value={"21:00"} />
      <option value={"22:00"} />
    </>
  );
  const [bathhouse, setBathhouse] = useState({
    active: false,
    time: "09:00",
    date: "",
    broom: {
      active: false,
      broomOrdinary: { active: false, oak: 0, birch: 0 },
      broomAromatic: { active: false, wormwood: 0, tributary: 0, mata: 0 },
    },
    drinkAndTreats: {active: false, order: ''}
  });
  const [chernyean, setChernyean] = useState(false);
  const [gazebos, setGazebos] = useState(false);
  const [crib, setCrib] = useState({ active: false, count: 1 });
  const [orderServices, setOrderServices] = useState({
    pet: false,
    massage: false,
    manicure: false,
    businessExcursion: false,
  });
  const [house, setHouse] = useState("#1");
  const [FIO, setFIO] = useState("Иванов Иван Иванович");
  //   const [gazebos, setGazebos] = useState(false);
  //   const [gazebos, setGazebos] = useState(false);
  const [bathhouseTime, setBathhouseTime] = useState("00:00");
  const [chernyeanTime, setChernyeanTime] = useState("00:00");
  const [gazebosTime, setGazebosTime] = useState("00:00");

  const [dateResidence, setDateResidence] = useState({
    From: "2021-01-01",
    To: "2021-01-01",
  });

  // onChange={(e) => setMeal(e.target.value)}
  const [addFields, setAddFields] = useState({
    arrivalTime: "00:00",
    meal: { active: false, time: "00:00" },
    barbecue: { active: false, text: "" },
    banquet: { active: false, text: "" },
    houseWishes: { active: false, text: "" },
    extraSpace: { active: false, count: 1 },
  });

  const OutCheckbox = (e, sets = Function.prototype) => {
    setTimeout(sets(e), 1000);
  };

  const toDay = new Date();
  const dates =
    toDay.getFullYear() +
    "-" +
    (toDay.getMonth() + 1 < 10
      ? "0" + toDay.getMonth() + 1
      : toDay.getMonth() + 1) +
    "-" +
    (toDay.getDate() < 10 ? "0" + toDay.getDate() : toDay.getDate());
  //Данные для вывода
  const value = {
    dateResidence,
    setDateResidence,
    setGazebosTime,
    setBathhouse,
    setChernyean,
    setGazebos,
    setBathhouseTime,
    setChernyeanTime,
    setHouse,
    OutCheckbox,
    setCrib,
    setFIO,
    setOrderServices,
    LISTTIME,
    bathhouse,
    orderServices,
    crib,
    house,
    chernyean,
    gazebos,
    bathhouseTime,
    chernyeanTime,
    gazebosTime,
    FIO,
    addFields,
    setAddFields,
    dates,
  };

  return (
    <CostomContext.Provider value={value}>
      {props.children}
    </CostomContext.Provider>
  );
};
